@import '../../../common.scss';

.flipCardRightContent {
    height: 190px;
    width: 282px;

    .turnBoxContainer {
        width: 100%;
        height: 100%;
        transition: all 200ms linear 0ms;
        perspective: 600px;
        position: relative;
        overflow: hidden;

        .turnBoxFace {
            transform-origin: 50% 50%;
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            color: $color-white;
            transition: all 500ms ease 0ms;

            .turnBoxContent1 {
                width: 100%;
                height: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                position: relative;

                .turnBoxItemClass {
                    font-size: 9px;
                    font-family: sans-serif;
                    font-weight: normal;
                }

                .turnBoxItemText {
                    font-size: 33px;
                    font-family: "Roslindale";
                    font-weight: 600;
                    letter-spacing: 1.5px;
                    margin-bottom: 3px;
                }

                .turnBoxItemPhonetic {
                    font-size: 21px;
                    font-family: sans-serif;
                    font-weight: normal;
                    color: $color-red-3;
                }

                .turnBoxItemDate {
                    display: none;
                }
            }

            .turnBoxContent2 {
                width: 100%;
                height: 100%;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                justify-content: center;
                position: relative;
                padding-left: 12px;

                .turnBoxItemMeaning {
                    font-family: "Roslindale";
                    letter-spacing: 1px;
                    font-size: 14px;
                    font-weight: 600;
                    color: $color-white;
                    text-align: left;
                    white-space: break-spaces;
                    cursor: default;

                    .turnBoxItemMeaningClass {
                        margin-left: -6px;
                        margin-bottom: 3px;
                    }

                    .turnBoxItemMeaningText {
                        margin-bottom: 2px;

                        &::before {
                            content: "";
                            display: inline-block;
                            margin-right: 2px;
                            width: 15px;
                            height: 15px;
                            background: url("../../../assets/main/clover.png");
                            background-size: contain;
                            vertical-align: bottom;
                            transform: rotate(-18deg);
                        }
                    }
                }

                .turnBoxSign {
                    position: absolute;
                    bottom: 0px;
                    right: 3px;
                }
            }
        }

        .turnBoxItemProgress {
            position: absolute;
            top: 12px;
            right: 15px;
            font-family: "Roslindale";
            color: $color-red-1;
            font-size: 15px;
            position: absolute;
            top: 6px;
            right: 6px;
            z-index: 99;
        }

        .turnBoxFaceNum1 {
            transform: rotateX(0deg) translate3d(0px, 0px, 0px);
            z-index: 20;
            opacity: 1;
        }

        .turnBoxFaceNum2 {
            transform: rotateX(90deg) translate3d(0px, -95px, 95px);
            z-index: 10;
            opacity: 0;
        }
    }

    .turnBoxContainer:hover .turnBoxFaceNum1,
    .turnBoxHover .turnBoxFaceNum1 {
        transform: rotateX(-90deg) translate3d(0px, 95px, 95px);
        z-index: 10;
        opacity: 0;
    }

    .turnBoxContainer:hover .turnBoxFaceNum2,
    .turnBoxHover .turnBoxFaceNum2 {
        transform: rotateX(0deg) translate3d(0px, 0px, 0px);
        z-index: 20;
        opacity: 1;
    }
}