@import "../../../common.scss";


.definition {
    @include baseBody;
    max-width: 360px;

    .definitionTop {
        position: absolute;
        top: 0;
        right: 0;
        display: flex;
        padding: 1px;

        .definitionBtn {
            @include baseButton(28px);
        }
    }

    .definitionHeader {
        border-bottom: 1px solid #343434;

        .definitionHeaderIndex {
            font-family: "nyt-cheltenham";
            font-weight: 500;
            font-size: 13px;
        }

        span:nth-child(2) {
            font-size: 16px;
            line-height: 18px;
            font-family: "nyt-cheltenham";
            font-weight: 500;
            color: $color-black;
            margin: 0 3px;
        }

        span:nth-child(3) {
            color: #727272;
            font-size: 10px;
            font-style: italic;
            font-weight: 500;
        }
    }

    .definitionBody {
        .sn-g {
            &:not(:last-child) {
                padding: 2px 0;
                border-bottom: 1px solid #343434;
            }

            .thumb {
                display: block;
                margin: 3px 0 3px 9px;
                max-height: 150px;
            }

            .num {
                font-family: "nyt-cheltenham";
                margin-right: 4px;
                font-weight: 700;
                display: inline-block;
                width: 11px;
                height: 11px;
                border-radius: 50%;
                text-align: center;
                font-size: 7.5px;
                margin-bottom: 1px;
                line-height: 9px;
                vertical-align: middle;
                border: 1px solid #707070;
            }

            .x {
                display: block;
                font-size: 12px;
                font-family: "nyt-imperial";
                font-weight: 500;
                line-height: 16px;
                margin: 2px 0;
                font-style: italic;

                &::before {
                    content: "";
                    display: inline-block;
                    margin-right: 2px;
                    width: 15px;
                    height: 15px;
                    background: url(../../../assets/main/clover.png);
                    background-size: contain;
                    vertical-align: bottom;
                    transform: rotate(-18deg);
                }

                b {
                    font-weight: 600;
                    color: $color-red-1;
                }
            }

            .xr-gs {
                font-weight: 500;
                font-size: 13px;
                margin: 3px 0;
                display: block;
                text-transform: uppercase;
                color: #181818;

                small {
                    color: #727272;
                    text-transform: lowercase;
                }
            }
        }
    }



}