@import "../../common.scss";

.login {
    background: #070707;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    outline: none;

    .loginForm {
        height: 60%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}

.loginContainer {
    width: 360px;
    height: 100%;
    background: #070707;
    border-left: 1px solid $color-boder;
    border-right: 1px solid $color-boder;
}

.loginItem {
    display: flex;
    width: 60%;

    .loginInput {
        background: #fff;
        flex-grow: 1;
        padding: 3px 9px;
        height: 25px;
        font-size: 11px;
        border: 0;
        outline: 0;
        transition: all 0.3s ease;
        font-family: "basier", sans-serif;
        border-top-left-radius: 3px;
        border-bottom-left-radius: 3px;
        text-align: center;
    }

    .loginInputErr {
        border-left: 3px solid $color-red-1;
        background: #e5bcbc;
    }

    .loginBtn {
        width: 25px;
        height: 25px;
        border: 0;
        outline: 0;
        background: $color-red-3;
        cursor: pointer;
        border-top-right-radius: 3px;
        border-bottom-right-radius: 3px;

        i {
            font-size: 21px;
            line-height: 25px;
            text-align: center;
            color: #000;
        }
    }
}