@import '../../common.scss';

.vocabulary {
    min-width: 360px;
    max-width: 360px;
    height: 100vh;
    overflow: hidden;
    position: relative;
    outline: none;

    .flashCardContainer {
        min-height: 190px;
        max-height: 190px;
        width: 360px;
        background-color: $color-black;
        display: flex;
    }

    .myInputContainer {
        position: relative;
        display: flex;
        width: 100%;
        background: #0c0c0c;

        .myInputLeftOrnament {
            z-index: inherit;
            height: 25px;
            position: absolute;
            left: 0;
            top: 0;
        }

        .myInputRightOrnament {
            z-index: inherit;
            height: 25px;
            position: absolute;
            right: 0;
            top: 0;
        }

        .myInput {
            height: 25px;
            width: 50%;
            padding: 0 9px;
            font-size: 11px;
            background: none;
            border: 0;
            outline: 0;
            color: #fff;
            font-family: "basier", sans-serif;
        }

        .myInputTransContent {
            width: 50%;
            display: flex;
            align-items: center;
            justify-content: space-between;

            .myInput {
                flex-grow: 1;
            }

            .myInputBtn {
                cursor: pointer;
                transition: opacity 0.3s ease;
                margin-right: 3px;
                opacity: 0;
                z-index: 36;
                background-color: transparent;
                border: none;
                outline: none;

                img {
                    height: 18px;
                    margin-top: 3px;
                }
            }

            &:hover .myInputBtn {
                opacity: 1;
            }
        }
    }

    .vocabularyContainer {
        height: calc(100vh - 246px);
        width: 360px;
        overflow-y: scroll;
        overflow-x: hidden;
        background: $color-black;
        position: relative;

        &::-webkit-scrollbar {
            display: none;
        }

        .searchContainer {
            .my-item {
                display: flex;
                justify-content: space-between;
                align-items: center;

                small {
                    margin-right: 3px;
                    font-size: 8px;
                }

                font-family: "basier",
                sans-serif;
                color: $color-black;
                font-size: 12px;
                line-height: 1.5;
                padding: 0 6px;
                max-width: 360px;
                overflow: hidden;
                cursor: pointer;
                margin-bottom: 1px;
                background: #ffffff80;
                transition: all 0.3s ease;

                .itemText {
                    flex-grow: 1;
                    padding: 3px 0;
                    display: flex;
                    justify-content: space-between;
                    align-items: flex-end;
                }

                &:hover {
                    background-color: $color-white;
                    color: #48c78e;
                    font-weight: 500;
                    margin-left: 2px;
                    border-left: 2px solid #48c78e;
                }

                &:hover .itemNumb {
                    opacity: 1;
                }

            }

            .itemDeleteBtn {
                @include baseButton(28px);
                display: inline-flex;

                &:hover {
                    color: $color-red-3;
                }

            }

            .itemNumb {
                font-size: 10px;
                opacity: 0;
                margin-right: 3px;
            }
        }
    }
}


.vocabularyBtnContainer {
    width: 60px;
    height: 60px;
    position: absolute;
    z-index: 99;
    bottom: 31px;
    right: 0;
}


.vocabularyBtn:nth-child(1) {
    transition: right 0.3s ease, bottom 0.3s ease;
    transition-delay: 0ms;
}

.vocabularyBtn:nth-child(2) {
    transition: right 0.3s ease, bottom 0.3s ease;
    transition-delay: 50ms;
}

.vocabularyBtn:nth-child(3) {
    transition: right 0.3s ease, bottom 0.3s ease;
    transition-delay: 100ms;
}

.vocabularyBtn:nth-child(4) {
    transition: right 0.3s ease, bottom 0.3s ease;
    transition-delay: 150ms;
}

.vocabularyBtn:nth-child(5) {
    transition: right 0.3s ease, bottom 0.3s ease;
    transition-delay: 200ms;
}

.vocabularyBtn:nth-child(6) {
    transition: right 0.3s ease, bottom 0.3s ease;
    transition-delay: 250ms;
}

.vocabularyBtn:nth-child(7) {
    transition: right 0.3s ease, bottom 0.3s ease;
    transition-delay: 300ms;
}

.vocabularyBtn:nth-child(8) {
    transition: right 0.3s ease, bottom 0.3s ease;
    transition-delay: 350ms;
}

.vocabularyBtn:nth-child(9) {
    transition: right 0.3s ease, bottom 0.3s ease;
    transition-delay: 100ms;
}

.vocabularyBtnContainer:hover .vocabularyBtn:nth-child(1) {
    right: 3px;
    bottom: 41px;
    opacity: 1;
    z-index: 36;
}

.vocabularyBtnContainer:hover .vocabularyBtn:nth-child(2) {
    right: 22px;
    bottom: 41px;
    opacity: 1;
    z-index: 36;
}

.vocabularyBtnContainer:hover .vocabularyBtn:nth-child(3) {
    right: 41px;
    bottom: 41px;
    opacity: 1;
    z-index: 36;
}

.vocabularyBtnContainer:hover .vocabularyBtn:nth-child(4) {
    right: 41px;
    bottom: 22px;
    opacity: 1;
    z-index: 36;
}

.vocabularyBtnContainer:hover .vocabularyBtn:nth-child(5) {
    right: 41px;
    bottom: 3px;
    opacity: 1;
    z-index: 36;
}

.vocabularyBtnContainer:hover .vocabularyBtn:nth-child(6) {
    right: 22px;
    bottom: 3px;
    opacity: 1;
    z-index: 36;
}

.vocabularyBtnContainer:hover .vocabularyBtn:nth-child(7) {
    right: 3px;
    bottom: 3px;
    opacity: 1;
    z-index: 36;
}

.vocabularyBtnContainer:hover .vocabularyBtn:nth-child(8) {
    right: 3px;
    bottom: 22px;
    opacity: 1;
    z-index: 36;
}

.vocabularyBtnContainer:hover .vocabularyBtn:nth-child(9) {
    right: 22px;
    bottom: 22px;
    opacity: 1;
    z-index: 36;
}

.vocabularyBtn {
    border-radius: 27%;
    position: inherit;
    right: 3px;
    bottom: 41px;
    z-index: 1;
    font-size: 14px;
    line-height: 1rem;
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
    text-align: center;
    color: #ffffff80;
    background-color: #212121;
    cursor: pointer;
    width: 16px;
    height: 16px;
    padding: 1px;
    border: 0;
    outline: 0;
    border-radius: 2px;
    opacity: 0;
    box-shadow: 0px 0px 9px #1c1c1c;
}

.vocabularyBtn:hover {
    transition: all 0.1s linear;
    color: #fff;
    box-shadow: 1px 1px 12px #121212;
    text-shadow: 0px 0px 6px #f4f4f4;
}

.timerBtn {
    font-size: 9px;
    font-family: "basier", sans-serif;
    font-weight: normal;
    line-height: 9px;
    text-align: center;
    padding: 3px 0;
    display: none;
    opacity: 1;
    cursor: pointer;
}

.timerBtnActive {
    display: block
}

.quoteContainer {
    @include baseBody;
    max-width: 360px;

    .quoteTop {
        position: absolute;
        top: 0;
        right: 0;
        display: flex;
        padding: 1px;

        .quoteBtnClose {
            @include baseButton(28px);
        }
    }

    .quoteHeader {
        display: flex;
        margin-bottom: 3px;

        .quoteBtn {
            @include baseButton(36px);

            i {
                font-size: 17px;
            }
        }

        .quoteBtnActive {
            color: #ffc107;

            &:hover {
                color: #ffc107;
            }
        }

        .quoteBtnCopied {
            color: $color-white;
        }
    }

    .quoteBody {
        font-size: 12.5px;
        line-height: 1.1rem;
        padding: 3px;
        border-top: 1px solid $color-boder;
    }
}