@import '../../common.scss';

.bottom {
    position: absolute;
    bottom: 0;
    min-width: 360px;
    max-width: 360px;
    height: 31px;
    border-top: 1px solid $color-boder;
    background-color: $color-background;
    display: flex;
    justify-content: space-around;
    align-items: center;

    a.active {
        color: $color-white;
    }

    .dotNum {
        color: rgba(124, 121, 121, 0.3);
        font-family: "Breitkopf";
        letter-spacing: 1px;
        font-size: 15px;
        height: 30px;
        width: 30px;
        line-height: 30px;
        text-align: center;
        cursor: default;
    }

    .dotNumActive {
        color: #7c7b7a;
    }

    .mainFooterBtn {
        display: block;
        flex-grow: 1;
        height: 100%;
        border: 0;
        outline: 0;
        cursor: pointer;
        font-family: "Roslindale";
        font-weight: normal;
        font-size: 11px;
        letter-spacing: 0.3px;
        color: #7c7b7a;
        line-height: 30px;
        text-align: center;
        overflow: hidden;
    }

    .mainFooterCornerBtn {
        height: 100%;
        width: 90px;
        background: url('../../assets/main/sunset.jpg');
        background-size: cover;
        background-position: center bottom;
        position: relative;
        cursor: pointer;
        z-index: 22;

        .inputWordRow {
            position: absolute;
            bottom: 0;
            right: 0;
            text-align: center;
            outline: 0;
            width: 27px;
            height: 12px;
            box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.6), 0px 0px 5px 0px rgba(0, 0, 0, 0.6) inset;
            border: 0;
            background: none;
            color: #ececec;
            display: inline-block;
            font-size: 8px;
            font-weight: bold;
            font-family: sans-serif;
            border-radius: 3px;
            z-index: 99;
        }

        .inputWordRowActive {
            box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.6), 0px 0px 5px 0px rgb(0 255 0 / 69%) inset;
        }

        .cornerDaily {
            font-family: 'Rubik';
            font-size: 9px;
            color: #ececec;
            line-height: 1px;
            font-weight: 600;
            height: 1px;
            position: absolute;
            right: 0;
            top: 0;
            z-index: 99;
            margin-top: -1px;
        }

        .cornerDailyActive {
            color: #00ff16;
        }
    }

    .mainFooterCornerBtnActive {
        background: url('../../assets/main/sunrise.jpg');
        background-size: cover;
        background-position: center bottom;
    }
}