@import "../../../common.scss";

.translate {
    max-width: 360px;

    .translateCard {
        @include baseBody;
        background: $color-calendar-bg;

        .translateTop {
            position: absolute;
            top: 0;
            right: 0;
            display: flex;
            padding: 1px;

            .translateBtnClose {
                @include baseButton(28px);
            }
        }

        .translateHeader {
            margin-bottom: 3px;

            .translateInput {
                font-size: 11px;
                height: 21px;
                width: 60%;
                padding-left: 3px;
                outline: 0;
                border: 0;
                border-radius: 3px;
                background: #0000001a;
            }
        }

        .translateBody {
            border-top: 1px solid $color-boder;
            padding: 3px;

            .translatePhonetic {
                padding-left: 77px;
            }

            .translateTranslation {
                font-family: 'Roslindale';
                font-size: 13px;
                color: #3e8ed0;
                text-align: center;
                text-transform: lowercase;
            }

            .translateContent {
                .translateContentItem {
                    margin-bottom: 2px;

                    p {
                        font-size: 13px;
                        font-family: 'Roslindale';
                        margin-bottom: 1px;
                        cursor: default;
                        display: inline-block;
                    }
                }

                .translateContentItemRow {
                    display: grid;
                    grid-template-columns: 2fr 3fr 1fr;
                    align-items: end;

                    span:nth-child(1) {
                        cursor: default;
                        margin-left: 2px;
                    }

                    span:nth-child(2) {
                        font-size: 10px;
                    }
                }

                .frequencyDiv {
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    .blueSpan {
                        background-color: rgb(26, 115, 232);
                        border-radius: 1px;
                        height: 6px;
                        margin: 1px;
                        width: 10px;
                    }

                    .whiteSpan {
                        background-color: rgb(218, 220, 224);
                        border-radius: 1px;
                        height: 6px;
                        margin: 1px;
                        width: 10px;
                    }
                }
            }
        }
    }
}