@import '../../../common.scss';

.flipCardLeftContent {
    width: 78px;
    height: 190px;

    .numberFlipContainer {
        width: 60px;
        height: 100%;
        float: right;
        border-left: 3px solid $color-white;
        border-right: 3px solid $color-white;

        .numberFlipContent {
            width: 100%;
            height: 100%;
            background-color: $color-red-2;
            border-left: 3px solid $color-black;
            border-right: 3px solid $color-black;
            padding-top: 66px;
        }
    }
}

.flipNumber {
    width: 42px;
    height: 42px;
    background-color: $color-white;
    border-radius: 50%;
    margin: 0 auto;
    padding: 6px;
    overflow: hidden;

    .numListContent {
        margin-top: 4px;
        margin-left: -3px;
        padding-top: 2px;
        width: 36px;
        height: 22px;
        display: flex;
        justify-content: center;
        overflow: hidden;

        .numList {
            transition: transform 300ms;

            .number {
                font-size: 16px;
                font-family: "Roslindale";
                font-weight: bold;
                line-height: 16px;
                text-align: center;
                color: $color-black;
                text-shadow: 0px 0px 1px #000000d1;
                margin: 2px 0;
                transition: opacity 500ms;
            }

            .numberVisible {
                opacity: 1;
            }

            .numberClose {
                opacity: 0.35;
            }

            .numberFar {
                opacity: 0.15;
            }

            .numberDistant {
                opacity: 0.1;
            }
        }

    }

    .imgFlipContent {
        width: 42px;
        height: 42px;

        .imgList {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            margin-left: -12px;
            margin-top: -50px;
            transition: transform 300ms;

            .imgNumber {
                font-size: 16px;
                font-family: "Roslindale";
                font-weight: bold;
                line-height: 48px;
                text-align: center;
                color: $color-black;
                text-shadow: 0px 0px 1px #000000d1;
                width: 100%;
            }

            .imgFlip {
                height: 42px;
            }
        }
    }
}