@import '../../common.scss';

.calendar {
    min-width: 360px;
    max-width: 360px;
    height: 100vh;
    overflow: hidden;
    position: relative;
    outline: 0;

    .calendarContainer {
        height: calc(100vh - 31px);
        width: 360px;
        overflow-y: scroll;
        overflow-x: hidden;
        display: flex;
        flex-direction: column;


        &::-webkit-scrollbar {
            display: none;
        }
    }

    .calendarImg {
        position: relative;
        text-align: center;

        .calendarImage {
            height: 270px;
            width: 360px;
            background-size: cover;
            background-position: center bottom;
        }

        .calendarQuote {
            cursor: default;
            height: 0;
            overflow: hidden;
            background-color: $color-calendar-bg;
            transition: height 0.2s ease;

            &::-webkit-scrollbar {
                display: none;
            }

            p {
                font-size: 9px;
                font-family: "nyt-cheltenham";
                color: $color-calendar-black;
                text-align: left;
                padding: 6px 0 6px 9px;
            }
        }

        .calendarQuoteHover {
            height: 60px;
        }

        .calendarDateContent {
            height: 57px;
            padding: 6px;
            cursor: pointer;
            background-color: #33333333;
            backdrop-filter: blur(6px);
            position: absolute;
            top: 213px;
            right: 0;

            p {
                font-family: 'Rubik';
                text-align: center;
                color: $color-calendar-white;
            }

            &:hover p {
                color: $color-white;
            }

            p:nth-child(1) {
                font-size: 11px;
                text-transform: uppercase;
            }

            p:nth-child(2) {
                font-size: 27px;
                line-height: 25px;
                font-family: 'Rubik';
            }

            p:nth-child(3) {
                font-size: 7px;
                line-height: 7px;
            }
        }

        .calendarDateContent:hover~.calendarQuote {
            height: 60px;
        }
    }

    .calendarSchedule {
        background: $color-calendar-bg;
        padding: 6px;
        font-family: "Rubik";
        flex-grow: 1;
        overflow-y: scroll;

        &::-webkit-scrollbar {
            display: none;
        }

        .calendarWeek {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            margin: 0 auto;

            .calendarWeekDay {
                background-color: $color-calendar-black;
                font-size: 12px;
                color: $color-calendar-white;
                font-weight: bold;
                line-height: 21px;
                text-align: center;
                text-transform: uppercase;
                cursor: default;
                height: 21px;
                width: 49px;

                &:first-child {
                    color: $color-red-1;
                }
            }
        }

        .calendarDays {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            margin: 0 auto;
            border-bottom: 1px dotted $color-calendar-border;

            .calendarDay {
                height: 21px;
                width: 49px;
                display: flex;
                justify-content: center;
                align-items: center;
                color: #383838;
                font-weight: bold;
                font-size: 13px;
                cursor: default;
                position: relative;

                .dayIndicateText {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;
                    margin-left: 2px;
                    color: $color-calendar-text;
                    font-size: 8px;

                    span:first-child {
                        text-decoration: underline;
                        text-underline-offset: 2px;
                        line-height: 11px;
                    }
                }

                .dayIndicateTextHidden {
                    font-size: 8px;
                    margin-right: 2px;
                    visibility: hidden;
                }

                .dayIndicateTextDone {
                    color: #383838;
                }
            }

            .calendarDayNot {
                color: $color-calendar-text;
            }

            .calendarSundayDay {
                color: $color-red-1;
            }

            .calendarTodayDay {
                cursor: pointer;

                .dayIndicateToday {
                    border: 1px solid #383838;
                    padding: 2px;
                    border-radius: 3px;
                    width: 23px;
                    text-align: center;
                }

                .dayIndicateText {
                    color: #00E676;
                }

            }

            .calendarSpecialDay {
                .dayIndicateToday {
                    text-decoration: underline;
                    text-decoration-thickness: 3px;
                    text-underline-offset: 1px;
                    text-decoration-color: #f90000;
                }
            }
        }
    }
}

.calendarHistory {
    height: 112px;
    width: 360px;

    .history {
        padding: 6px;
        background-color: $color-calendar-bg;

        .historyItem {
            width: 100%;
            display: flex;
            align-items: center;
            margin-bottom: 2px;
            font-family: "Rubik", sans-serif;
            font-size: 11px;

            .historyItemDesc {
                background: $color-calendar-black;
                width: 100px;
                height: 18px;
                color: #ffffff73;
                padding: 3px 6px;
                position: relative;
                cursor: pointer;

                &::after {
                    content: "";
                    width: 6px;
                    height: 10px;
                    position: absolute;
                    left: 100%;
                    top: 4px;
                    background: #161616;
                    clip-path: polygon(0% 0%, 100% 50%, 0% 100%);
                    z-index: 33;
                }
            }

            .historyItemDescComplete {
                color: $color-white;
                cursor: default;
            }

            .historyItemContent {
                height: 18px;
                flex-grow: 1;
                cursor: default;
                box-shadow: inset 0px 0px 18px #e7e7e7;
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 0 18px;

                span {
                    width: 100%;
                    text-align: center;
                }
            }

            .historyItemContentBtn {
                width: 60px;
                height: 18px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                cursor: pointer;
                border: 1px solid #bbbbbba3;
                padding: 0 3px;
                margin: 0 21px;
                background-color: #33333327;
                font-family: "Rubik", sans-serif;
                font-size: 11px;

                img {
                    height: 15px;
                }
            }

            .historyItemContentBtnActive {
                background-color: $color-white ;
            }
        }
    }
}


.popupContainer {
    width: 100%;
    background-color: $color-calendar-bg;
    position: relative;
    font-family: "Rubik", sans-serif;
    padding: 6px 9px;
    overflow: hidden;

    .popupContentTop {
        position: absolute;
        top: 0;
        right: 0;
        display: flex;
        padding: 1px;

        .popupBtn {
            @include baseButton(28px);
        }
    }

    .popupContentHeader {
        border-bottom: 1px solid $color-calendar-border;
        font-size: 12px;
        text-align: left;
        padding: 3px 0;
    }

    .popupContentBody {
        width: 100%;
        padding: 5px 0;

        .popupItem {
            width: 100%;
            display: flex;

            .popupInput {
                border: 1px solid $color-calendar-border;
                font-size: 12px;
                height: 18px;
                padding-left: 3px;
                border-radius: 3px;
                flex-grow: 1;
                outline: 0;
                background: none;
                margin: 0 1px;
            }

            .popupSelect {
                background: $color-calendar-black;
                color: $color-white;
                font-size: 11px;
            }

            .popupText {
                font-size: 11px;
                flex-grow: 1;
                height: 18px;
                border-radius: 3px;
                border: 1px solid #00000066;
                line-height: 18px;
                padding-left: 3px;
                background: $color-calendar-black;
                color: $color-white;
                cursor: default;
            }

            .popupCheckbox {
                display: flex;
                justify-content: center;
                font-size: 11px;
                flex-grow: 1;

                input {
                    cursor: pointer;
                    margin-right: 3px;
                    width: 12px;

                    &:checked {
                        accent-color: $color-calendar-black;
                    }
                }
            }

        }
    }

}