@import '../../../common.scss';


.edit {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 31px;
    right: 0;
    z-index: 36;
    outline: 0;
    background: $color-calendar-black;
    overflow-y: scroll;
    overflow-x: hidden;

    &::-webkit-scrollbar {
        display: none;
    }

    .editTop {
        position: inherit;
        right: 0;
        display: flex;
        padding: 1px;
        z-index: 99;

        .editBtnClose {
            @include baseButton(28px);
        }
    }

    .editCard {
        @include baseBody;

        .editInput {
            @include myInput;
            width: 100%;
            margin-top: 15px;
        }

        .searchContainer {
            .my-item {
                small {
                    margin-right: 3px;
                    font-size: 8px;
                }

                font-family: "basier",
                sans-serif;
                color: #000;
                display: block;
                font-size: 12px;
                line-height: 1.5;
                padding: 3px 6px;
                max-width: 360px;
                overflow: hidden;
                cursor: pointer;
                margin-bottom: 1px;
                background: #ffffff80;
                transition: all 0.3s ease;

                &:hover {
                    background-color: $color-white;
                    color: #48c78e;
                    font-weight: 500;
                    margin-left: 2px;
                    border-left: 2px solid #48c78e;
                }
            }
        }
    }

    .editCard {
        .editBtnResult {
            @include baseButton(50px);
            font-family: "nyt-imperial";
            text-transform: uppercase;
            font-size: 11px;
        }

        .editSerchResult {
            font-family: "Roslindale";
            font-size: 14px;
            text-align: center;
            text-transform: lowercase;
            width: 100%;
            margin: 3px 0 6px;
            background: 0;
            outline: 0;
            border: 0;
        }

        .editRowItem {
            display: grid;
            grid-template-columns: 1fr 4fr;
            align-items: center;
            margin-bottom: 3px;

            .editItemLabel {
                text-transform: uppercase;
            }

            .editItemInput {
                @include myInput;
                height: 18px;
                padding: 3px;
            }

            .editItemArea {
                resize: vertical;
                min-height: 60px;

                &::-webkit-scrollbar {
                    display: none;
                }
            }
        }
    }
}